  border-radius: 5px;
  cursor: pointer;
}

.register-form__avatar-preview {
  width: 74px;
  height: 74px;
  object-fit: cover;
  border-radius: 50%;
}

.register-form__is-pro-wrapper {
  margin-bottom: 18px;
}

.register-form__is-pro-label {
  margin-left: 10px;
  cursor: pointer;
}

.spinner {
  display: grid;
  place-content: center;
  z-index: 1000;
  cursor: wait;
  background-color: rgba(255, 255, 255, 0.5);
}

.spinner::before {
  content: "";
  display: block;
  border-radius: 50%;
  border: 6px solid #4285F4;
  box-sizing: border-box;
  animation: sweep 1s linear alternate infinite,
             rotate 0.8s linear infinite;
  width: 65px;
  height: 65px;
}

@keyframes rotate {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes sweep {
  0% {
    clip-path: polygon(0% 0%, 0% 0%, 0% 0%, 50% 50%, 0% 0%, 0% 0%, 0% 0%);
  }
  50% {
    clip-path: polygon(0% 0%, 0% 100%, 0% 100%, 50% 50%, 100% 0%, 100% 0%, 0% 0%);
  }
  100% {
    clip-path: polygon(0% 0%, 0% 100%, 100% 100%, 50% 50%, 100% 100%, 100% 0%, 0% 0%);
  }
}

